/* components/shared/Navbar.css */
.appLanding-nav {
  background-color: #001723;
  z-index: 1;
  border-bottom: 1px solid #004264;
}
.app-user-container {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 6.9vh;
  padding: 1vh -10px;
  right: 0;
  background-color: transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid transparent;
  z-index: 1000;
}

.dropdown-menu.show {
  display: block;
}

.dropdown-menu.show button {
  background-color: #e6a500;
  color: rgb(15, 15, 63);
  border: none;
  padding: 10px 2vh;
  text-align: left;
  width: 6vw; /* Ensure the button width adjusts based on the content */
  height: 6vh; /* Ensure the button height adjusts based on the content */
  cursor: pointer;
  border-radius: 1vh;
}

.dropdown-menu button:hover {
  background-color: #f1f1f1;
}


.appLanding-navbar-container {
  height: 55px;
  padding: 0.3vh;
  display: flex;
  justify-content: center; /* Use space-between to push the links and image to the edges */
  align-items: center;
  z-index: 1;
}
.appLanding-navbar-logo-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 50%;
  padding-left: 1vw;
}
.appLanding-navbar-details-container {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row;
  padding-right: 1vw;
  gap: 20px;
}
.appLanding-navbar-logo-container img {
  height: auto;
  cursor: pointer;
  animation-name: loadIn1;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
}
@keyframes loadIn1 {
  0% {
    transform: skew(10deg, 0deg) scaleX(1.2);
  }
  100% {
    transform: skew(0deg, 0deg) scaleX(1);
  }
}

.appLanding-user {
  font-size: 35px;
  font-weight: 800;
  color: aliceblue;
  background-color: #001723;
  width: 35px;
  height: auto;
  border-radius: 20px;
}

.appLanding-navbar-details-container button {
  width: 130px;
  height: 5.5vh;
  border-radius: 50px;
  font-weight: 400;
  text-align: center;
  background-color: #004264;
  color: rgb(255, 255, 255, 0.8);
  border: 2px solid #004264;
}

.appLanding-navbar-details-container button:hover {
  color: #004264;
  background-color: rgb(255, 255, 255, 0.8);
}

.desktop-logo {
  display: block; 
  width: 130px;
}

.mobile-logo {
  display: none; /* Hide the mobile logo by default */
}

@media screen and (max-width: 600px) {
  .appLanding-navbar-container {
    width: 100vw;
    height: 50px;
  }
  .appLanding-navbar-logo-container img {
    background-size: contain;
    content: "";

  }
  .appLanding-navbar-details-container .history-icon {
    font-size: x-large;
    padding: 0;
    margin: 0;
  }
  .appLanding-navbar-details-container .appLanding-user {
    background-color: green;
  }

  .appLanding-navbar-details-container .appLanding-history-button {
    border: none;
    background: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    width: auto;
    height: auto;
  }
  .appLanding-navbar-details-container button span {
    display: none;
  }
  .desktop-logo {
    display: none;
  }
  .mobile-logo {
    width: 40px;
    display: block; 
  }
}
