/* Common styles for all screen sizes */
.login-container {
    background: #001b28;
    background-size: cover;
    background-position: center;
    /* min-height: 92.5vh; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-image-container {
    height: 93vh;
    width: 50%;
    /* background-color: #001b28;    */
}

.login-details-container {
    height: 93vh;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper {
    width: auto; /* Adjusted width for responsiveness */
    background: transparent;
    border: 2px solid #004264;
    box-shadow: 0 0 10px rgba(0, 0, 0, .2);
    color: white;
    border-radius: 10px;
    padding: 20px;
    opacity: 1;
    /* background-color: #001723; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px
}

.wrapper h1 {
    font-size: 36px;
    text-align: center;
    height: 38px;
    width: 350px;
    margin: 0px;
    padding: 5px;
}

.wrapper form {
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    width: 100%;
}

.input-box {
    height: auto;
    width: 100%;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
}

.input-box p {
    margin: 0px 0px 5px 0px;
}
.details {
    width: 100%;
    padding-right: 15px;
}
.details input {
    width: 100%;
    height: 20px;
    background: #001b28;
    border: none;
    outline: none;
    border: 1px solid #004264;
    border-radius: 30px;
    font-size: 16px;
    padding: 5px 10px 5px 10px;
    color: white;
}

.details input::placeholder {
    color: #3D4856;
}

.forgot {
    display: flex;
    align-self: flex-end;
    font-size: 12px;
    margin: 10px 0 15px 0px; /* Adjusted margin-top */
}

.forgot a {
    color: #E6A500;
    text-decoration:none;
}

.forgot a:hover {
    text-decoration: underline;
    cursor: pointer;
}

.login-button {
    width: 100%;
    display: flex;
    justify-content: center;
}

.login-button button {
    width: 100%;
    height: 35px;
    background: #E6A500;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    cursor: pointer;
    font-size: 18px;
    color: white;
    font-weight: 700;
}

.register-link {
    display: flex;
    align-self: flex-start;
    font-size: 12px;
    margin: 10px 0 10px;
}

.register-link p {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    margin: 0px;
}
.register-link a {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
}

.register-link a:hover {
    text-decoration: underline;
    cursor: pointer;
}

.google-login {
    display: flex;
    justify-content: center;
}

.microsoft-login img{
    background:green;
    border-radius: 6px;
}

/* Responsive styles using media queries */
@media screen and (max-width: 600px) {
    .main-container{
        height: 100vh;
        width: 100vw;
        display: flex;
        flex-direction: column;
    }
    .login-container{
        height:100%;
        width: 100vw;
        flex-direction: column;
    }
    .login-image-container {
        height: 40%;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .login-image-container img {
        height: 40vh;
        width: 80%;
        /* background-color: #001b28; */
        
    }
    .login-details-container {
        height:40%;
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .wrapper {
        width: 80%;
        height: max-content; 
    }
}
